import React, { useState, useEffect } from 'react'

import MainButton from '../../components/Button/MainButton'
import SecondaryButton from '../../components/Button/SecondaryButton'

import './Contact.scss'

const Contact = () => {
	const [scrollPosition, setScrollPosition] = useState(0)
	const [headerTranslateY, setHeaderTranslateY] = useState(400) // Start off-screen
	const handleScroll = () => {
		const position = window.pageYOffset
		const windowHeight = document.documentElement.scrollHeight - window.innerHeight
		const scrolledPercentage = (position / windowHeight) * 100

		// Zakładając, że efekt ma się zacząć na 80% wysokości strony i zakończyć na 100%.
		const startEffectAt = 80 // Procent wysokości strony, gdzie efekt się zaczyna.
		const endEffectAt = 100 // Procent wysokości strony, gdzie efekt się kończy.

		if (scrolledPercentage > startEffectAt && scrolledPercentage <= endEffectAt) {
			const effectRange = endEffectAt - startEffectAt
			const effectProgress = (scrolledPercentage - startEffectAt) / effectRange
			const translateYValue = -250 * effectProgress // Od 0 do -50px.
			setScrollPosition(translateYValue)
		} else if (scrolledPercentage <= startEffectAt) {
			setScrollPosition(0)
		}

		// Dla contact-header
		const headerStartEffectAt = 90 // Zacznij efekt później niż dla contactEllipse-light
		if (scrolledPercentage > headerStartEffectAt && scrolledPercentage <= endEffectAt) {
			const effectRange = endEffectAt - headerStartEffectAt
			const effectProgress = (scrolledPercentage - headerStartEffectAt) / effectRange
			const headerTranslateYValue = 400 - 400 * effectProgress // Od 400px do 0px
			setHeaderTranslateY(headerTranslateYValue)
		} else if (scrolledPercentage <= headerStartEffectAt) {
			setHeaderTranslateY(400) // Utrzymaj początkową wartość, gdy użytkownik nie przewinął wystarczająco
		}
	}

	useEffect(() => {
		window.addEventListener('scroll', handleScroll)

		return () => {
			window.removeEventListener('scroll', handleScroll)
		}
	}, [])

	const ellipseLightStyle = {
		transform: `translateY(${scrollPosition}px)`,
	}

	const headerStyle = {
		transform: `translateY(${headerTranslateY}px)`,
	}

	return (
		<div className='contact sectionWide'>
			<div
				className='contact-header'
				style={headerStyle}>
				<p className='contact-header-main text-gradient'>Reach out to me!</p>
				<MainButton text='Press me' />
			</div>
			<div className='contactEllipse'>
				<div className='ellipseContent'>
					<div className='ellipseContent-center'>
						<p className='ellipseContent-text'>Contact at:</p>
						<p className='ellipseContent-main'>dev@rafalpaurowicz.pl</p>
					</div>
					<div className='ellipseContent-center'>
						<p className='ellipseContent-text'>
							Or drop us a line, <br />
							anytime:
						</p>
						<a
							href='www.anytime.studio'
							className='ellipseContent-main'>
							Anytime.studio
						</a>
					</div>
					<SecondaryButton text='Go to top' />
					<div className=''>
						<p className='ellipseContent-small'>This is disclaimer text, disclaiming, that the site was done by me</p>
					</div>
				</div>
			</div>
			<div
				className={`contactEllipse-light`}
				style={ellipseLightStyle}></div>
		</div>
	)
}

export default Contact
